.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  padding: 3%;
  max-height: 20%;
  color: white;
  z-index: 0;
}
main {
  background:color #222;
  overflow-y: scroll
}

.App-title {
  font-size: 2.3em;
  font-family: serif;
  display: inline;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

main {
  position: fixed;
  z-index: 5;
  bottom: 0;
  height: 80%;
  width: 100%;
}
.Tracks {
  width: 98.75%;
  height: 100%;
  float: left;
  color: #FFF
}
.track {
  margin: 0;
  height: 20%;
  width: 100%;
  border: solid #FFF;
  border-width: 3px 1px
}

div.instrument {
  border: 1px solid #222;
  height: 100%;
}

/* two pieces of an instrument track */
.instrumentControl {
  width: 19%;
  height: 100%;
  float: left;
}
.Soundboard {
  width: 81%;
  height: 100%;
  float: left;
  background-color: white;
}

div.instrument.active .instrumentControl {
  background-color: #222;
  color: #FFF;
}
div.instrument.inactive .instrumentControl {
  background-color: white;
  color: #FFF;
}

/* soundboard */
.instrument .Soundboard .row {
  height: 100%;
}

.instrument .Soundboard .row .beatDivision {
  float: left;
  border: 1px solid #222;
}
.instrument .Soundboard .row .beatDivision.active {
  background-color: #222;
  color: white;
}
.instrument .Soundboard .row .beatDivision.inactive {
  background-color: #FFF;
  color: black;
}

.instrument .Soundboard .row .bar {
  height: 86%;
  width: 24.55%;
  border: 2px solid #222;
  float: left;
}

.instrument.n0 .Soundboard .row .bar {
  height: 73%;
}

.instrument.n1 .Soundboard .row .bar {
  height: 76%;
}

body {
  margin: 100;
  padding: 0;
  font-family: serif;
}

